import React from "react";
import Flux from '@4geeksacademy/react-flux-dash';
import { Panel } from '../../utils/bc-components/index';
import AdminStore from '../../stores/AdminStore';

export default class Add extends Flux.View {
    
    constructor(){
        super();
        this.state = {
            user: null
        };
        this.formData = {
            first_name: null
        }
        this.bindStore(AdminStore, 'users', () => {
            this.updateData();
        });
    }
    
    updateData(){
        const user = AdminStore.getSingleUser(this.props.match.params.user_id);
        this.setState({ user });
        this.formData = {
            first_name: user.full_name
        }
    }
    
    componentDidMount(){
        this.updateData();
    }
    
    
  
  render() {
    return (
        <div className="with-padding">
            <Panel style={{padding: "10px"}} zDepth={1}>
                <h1>Add</h1>
                { (!this.state.user) ? "Loading..." :
                    <form>
                        <div className="row">
                            <div className="col">
                                <input type="text" className="form-control" placeholder="First name" value={this.formData.first_name} />
                            </div>
                            <div className="col">
                                <input type="text" className="form-control" placeholder="Last name" />
                            </div>
                        </div>
                    </form>
                }
            </Panel>
        </div>
    );
  }
}