/* global localStorage */
import Flux from '@4geeksacademy/react-flux-dash';

class AdminStore extends Flux.Store{
    constructor(){
        super();
        
        this.state = {
            users: null
        };
    }
    
    _setUsers(users){ this.setStoreState({ users }).emit('users'); }
    getUsers(){ return this.state.users; }
    getSingleUser(id){ 
        if(!this.state.users) return null;
        
        let users = this.state.users.filter((user) => user.id === id); 
        if(users.length === 1) return users[0]; 
        else if(users.length === 0) return null;
        else if(users.length >1) throw new Error('There seems to be more than one user with the id '+id);
    }
    
}
export default new AdminStore();